import template from './dao-input.html';
import './dao-input.scss';

function DaoInputDirective() {
  /*ngInject*/

  return {
    restrict: 'E',
    scope: {
      state: '=',
      message: '@',
      placeholder: '@',
      hasButton: '@',
    },
    template: template,
    transclude: true,
    compile: DaoInputCompile,
    link: DaoInputLink,
    //controller: DaoInputController,
    //controllerAs: 'vm',
    //bindToController: true,
  };
}

function DaoInputCompile() {

}

function DaoInputLink() {

}

//function DaoInputController() {
//  var vm = this;
//
//  console.log(vm);
//}

export default DaoInputDirective;



