import popoverTpl from './popover.html';
import popoverTemplateTpl from './popover-template.html';
import popoverHtmlTpl from './popover-html.html';


/*@ngInject*/
export function daoPopoverTemplatePopup() {
  return {
    replace: true,
    scope: {
      title: '@',
      contentExp: '&',
      placement: '@',
      popupClass: '@',
      animation: '&',
      isOpen: '&',
      originScope: '&',
    },
    template: popoverTemplateTpl,
  };
}

/*@ngInject*/
export function daoPopoverTemplate($daoTooltip) {
  return $daoTooltip('daoPopoverTemplate', 'popover', 'click', {
    useContentExp: true,
  });
}

/*@ngInject*/
export function daoPopoverHtmlPopup() {
  return {
    replace: true,
    scope: {
      contentExp: '&',
      title: '@',
      placement: '@',
      popupClass: '@',
      animation: '&',
      isOpen: '&',
    },
    template: popoverHtmlTpl,
  };
}

/*@ngInject*/
export function daoPopoverHtml($daoTooltip) {
  return $daoTooltip('daoPopoverHtml', 'popover', 'click', {
    useContentExp: true,
  });
}

/*@ngInject*/
export function daoPopoverPopup() {
  return {
    replace: true,
    scope: {
      title: '@',
      content: '@',
      placement: '@',
      popupClass: '@',
      animation: '&',
      isOpen: '&',
    },
    template: popoverTpl,
  };
}

/*@ngInject*/
export function daoPopover($daoTooltip) {
  return $daoTooltip('daoPopover', 'popover', 'click');
}



