import template from './dao-demo.html';
import './dao-demo.scss';

class DaoDemoBlockDirective {
  constructor() {
    'ngInject';

    this.scope = {
      title: '@',
    }; // 
    this.template = template; // html 
    this.restrict         = 'E';
    this.transclude       = true;

    this.controller       = DaoDemoBlockController;
    this.controllerAs     = 'dao';
    this.bindToController = true;
  }

}

class DaoDemoBlockController {
  constructor() {
    'ngInject';
  }
}

export default DaoDemoBlockDirective;




